.onlineOrders {
  /* margin: 0 1em 4em 2em; */
  margin: 0 1em;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(430px, 1fr));
  /* width: 95%; */
}
.onlineOrders h1 {
  margin: 0.5em 0;
}
.onlineOrders .details {
  margin: 1em;
  padding: 0.5em;
  /* font-size: 1em */
  border: 2px solid #db952377;
  border-radius: 10px;
  box-shadow: 0 0 10px #DB9423;
}
.onlineOrders .details .InfoProcess {
  display: flex;
  /* justify-content: space-around; */
}
.onlineOrders .details .InfoProcess .info {
  width: 15em;
  /* margin-right: 2em; */
}
.onlineOrders .details .pizzasIndividuales,
.onlineOrders .details .pizzasPromociones {
  margin: 0.5em 0;
  padding-left: 0.5em;
}
.onlineOrders .details .pizzasInfoIndividuales {
  padding-left: 0.5em;
}
.onlineOrders .details .pizzasPromociones .pizzasPromocionesDetails {
  margin: 0.5em 0;
  padding-left: 0.5em;
}
.onlineOrders .details .pizzasPromociones .pizzasPromocionesDetails .pizzasInfoPromociones {
  padding-left: 0.5em;
}